
let oldPos = window.scrollY;
window.onscroll = function() {
    let newPos = window.scrollY;
    let stickyElement = document.querySelector('.sticky-element');
    if (newPos > oldPos && stickyElement.offsetHeight > window.innerHeight) {
        stickyElement.style.top = window.innerHeight - stickyElement.offsetHeight + 'px';
        oldPos = newPos;
    }
    if (newPos < oldPos) {
        stickyElement.style.top = '0';
        oldPos = newPos;
    }
}